"use client"

import { useState, useRef } from "react"
import { AppSidebar } from "@/components/app-sidebar"
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Separator } from "@/components/ui/separator"
import { Slider } from "@/components/ui/slider"
import {
  SidebarInset,
  SidebarProvider,
  SidebarTrigger,
} from "@/components/ui/sidebar"
import {
  QrCode,
  Type,
  Image as ImageIcon,
  Download,
  Plus,
  Settings,
  Move,
  Trash2,
} from "lucide-react"
import { QRCodeSVG } from "qrcode.react"
import jsPDF from "jspdf"

// Mock assets for demonstration
const mockAssets = [
  { id: "PC-001", name: "Dell OptiPlex 7090", location: "Office A-101" },
  { id: "PC-002", name: "MacBook Pro 16\"", location: "Office A-102" },
  { id: "PC-003", name: "HP EliteDesk 800", location: "Office A-103" },
  { id: "PC-004", name: "Lenovo ThinkPad X1", location: "Office A-104" },
]

interface LabelElement {
  id: string
  type: "qr" | "text" | "image"
  content: string
  fontSize: number
  x: number
  y: number
  width: number
  height: number
}

interface PrintSettings {
  paperFormat: "A4" | "Letter" | "Custom"
  customWidth: number
  customHeight: number
  labelsPerRow: number
  labelsPerColumn: number
  labelWidth: number
  labelHeight: number
  topMargin: number
  leftMargin: number
  horizontalSpacing: number
  verticalSpacing: number
}

export default function Page() {
  const [selectedElement, setSelectedElement] = useState<string | null>(null)
  const [qrContent, setQrContent] = useState("PC-001")

  const [elements, setElements] = useState<LabelElement[]>([
    {
      id: "qr-1",
      type: "qr",
      content: "PC-001",
      fontSize: 12,
      x: 10,
      y: 10,
      width: 50,
      height: 50,
    },
    {
      id: "text-1",
      type: "text",
      content: "Dell OptiPlex 7090",
      fontSize: 12,
      x: 70,
      y: 25,
      width: 100,
      height: 20,
    },
  ])

  const [printSettings, setPrintSettings] = useState<PrintSettings>({
    paperFormat: "A4",
    customWidth: 210,
    customHeight: 297,
    labelsPerRow: 3,
    labelsPerColumn: 8,
    labelWidth: 60,
    labelHeight: 30,
    topMargin: 15,
    leftMargin: 10,
    horizontalSpacing: 5,
    verticalSpacing: 3,
  })

  // Calculate scale factor for preview
  const maxPreviewWidth = 400
  const maxPreviewHeight = 300
  const scaleX = maxPreviewWidth / printSettings.labelWidth
  const scaleY = maxPreviewHeight / printSettings.labelHeight
  const previewScale = Math.min(scaleX, scaleY, 3) // Max 3x scale

  const previewWidth = printSettings.labelWidth * previewScale
  const previewHeight = printSettings.labelHeight * previewScale

  const addElement = (type: "qr" | "text" | "image") => {
    const newElement: LabelElement = {
      id: `${type}-${Date.now()}`,
      type,
      content:
        type === "qr"
          ? qrContent
          : type === "text"
          ? "New Text"
          : "",
      fontSize: 12,
      x: 10,
      y: 10 + elements.length * 25,
      width: type === "qr" ? 30 : 80,
      height: type === "qr" ? 30 : 15,
    }
    setElements([...elements, newElement])
    setSelectedElement(newElement.id)
  }

  const updateElement = (id: string, updates: Partial<LabelElement>) => {
    setElements(
      elements.map((el) => (el.id === id ? { ...el, ...updates } : el))
    )
  }

  const deleteElement = (id: string) => {
    setElements(elements.filter((el) => el.id !== id))
    if (selectedElement === id) {
      setSelectedElement(null)
    }
  }

  const exportToPDF = async () => {
    const pdf = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format:
        printSettings.paperFormat === "Custom"
          ? [printSettings.customWidth, printSettings.customHeight]
          : printSettings.paperFormat.toLowerCase() as any,
    })

    const pageWidth =
      printSettings.paperFormat === "Custom"
        ? printSettings.customWidth
        : printSettings.paperFormat === "A4"
        ? 210
        : 215.9

    const pageHeight =
      printSettings.paperFormat === "Custom"
        ? printSettings.customHeight
        : printSettings.paperFormat === "A4"
        ? 297
        : 279.4

    let assetIndex = 0
    const totalLabels = printSettings.labelsPerRow * printSettings.labelsPerColumn

    for (let row = 0; row < printSettings.labelsPerColumn; row++) {
      for (let col = 0; col < printSettings.labelsPerRow; col++) {
        if (assetIndex >= mockAssets.length) break

        const asset = mockAssets[assetIndex]
        const x =
          printSettings.leftMargin +
          col * (printSettings.labelWidth + printSettings.horizontalSpacing)
        const y =
          printSettings.topMargin +
          row * (printSettings.labelHeight + printSettings.verticalSpacing)

        // Draw label border (for reference)
        pdf.setDrawColor(200, 200, 200)
        pdf.rect(x, y, printSettings.labelWidth, printSettings.labelHeight)

        // Generate QR code
        const qrCanvas = document.createElement("canvas")
        const qrCode = new (window as any).QRCode(qrCanvas, {
          text: asset.id,
          width: 200,
          height: 200,
          correctLevel: (window as any).QRCode.CorrectLevel.M,
        })

        // Add QR code to PDF
        const qrImageData = qrCanvas.toDataURL("image/png")
        const qrSize = Math.min(
          printSettings.labelWidth * 0.4,
          printSettings.labelHeight * 0.8
        )
        pdf.addImage(qrImageData, "PNG", x + 2, y + 2, qrSize, qrSize)

        // Add text
        pdf.setFontSize(8)
        pdf.setTextColor(0, 0, 0)
        pdf.text(asset.name, x + qrSize + 4, y + 8)
        pdf.setFontSize(6)
        pdf.text(asset.id, x + qrSize + 4, y + 15)
        pdf.text(asset.location, x + qrSize + 4, y + 20)

        assetIndex++
      }
    }

    // Create blob and open in new tab
    const pdfBlob = pdf.output("blob")
    const blobUrl = URL.createObjectURL(pdfBlob)
    window.open(blobUrl, "_blank")

    // Clean up the blob URL after a short delay
    setTimeout(() => {
      URL.revokeObjectURL(blobUrl)
    }, 1000)
  }

  const selectedEl = elements.find((el) => el.id === selectedElement)

  return (
    <SidebarProvider>
      <AppSidebar />
      <SidebarInset>
        <header className="flex h-16 shrink-0 items-center gap-2 justify-between">
          <div className="flex items-center gap-2 px-4">
            <SidebarTrigger className="-ml-1" />
            <Separator orientation="vertical" className="mr-2 h-4" />
            <Breadcrumb>
              <BreadcrumbList>
                <BreadcrumbItem className="hidden md:block">
                  <BreadcrumbLink href="/admin">Admin</BreadcrumbLink>
                </BreadcrumbItem>
                <BreadcrumbSeparator className="hidden md:block" />
                <BreadcrumbItem>
                  <BreadcrumbPage>QR Label Generator</BreadcrumbPage>
                </BreadcrumbItem>
              </BreadcrumbList>
            </Breadcrumb>
          </div>
          <div className="px-4">
            <Button onClick={exportToPDF} className="gap-2">
              <Download className="h-4 w-4" />
              PDF Exportieren
            </Button>
          </div>
        </header>

        <div className="flex flex-1 gap-4 p-4 pt-0">
          {/* Left Column - Toolbox */}
          <div className="w-64 space-y-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-sm flex items-center gap-2">
                  <Plus className="h-4 w-4" />
                  Elemente hinzufgen
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                <Button
                  variant="outline"
                  className="w-full justify-start gap-2"
                  onClick={() => addElement("qr")}
                >
                  <QrCode className="h-4 w-4" />
                  QR-Code
                </Button>
                <Button
                  variant="outline"
                  className="w-full justify-start gap-2"
                  onClick={() => addElement("text")}
                >
                  <Type className="h-4 w-4" />
                  Textfeld
                </Button>
                <Button
                  variant="outline"
                  className="w-full justify-start gap-2"
                  onClick={() => addElement("image")}
                >
                  <ImageIcon className="h-4 w-4" />
                  Bild/Logo
                </Button>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-sm">QR-Code Inhalt</CardTitle>
              </CardHeader>
              <CardContent>
                <Input
                  value={qrContent}
                  onChange={(e) => setQrContent(e.target.value)}
                  placeholder="Asset ID"
                />
              </CardContent>
            </Card>
          </div>

          {/* Center Column - Label Designer */}
          <div className="flex-1 flex flex-col items-center justify-center">
            <Card className="w-full max-w-2xl">
              <CardHeader>
                <CardTitle className="text-center">Label Designer</CardTitle>
                <CardDescription className="text-center">
                  {printSettings.labelWidth}  {printSettings.labelHeight} mm
                  (Skaliert: {Math.round(previewScale * 100)}%)
                </CardDescription>
              </CardHeader>
              <CardContent className="flex justify-center">
                <div
                  className="relative border-2 border-dashed border-gray-300 bg-white"
                  style={{
                    width: `${previewWidth}px`,
                    height: `${previewHeight}px`,
                  }}
                >
                  {elements.map((element) => (
                    <div
                      key={element.id}
                      className={`absolute cursor-pointer border-2 ${
                        selectedElement === element.id
                          ? "border-blue-500"
                          : "border-transparent"
                      } hover:border-blue-300`}
                      style={{
                        left: `${element.x * previewScale}px`,
                        top: `${element.y * previewScale}px`,
                        width: `${element.width * previewScale}px`,
                        height: `${element.height * previewScale}px`,
                      }}
                      onClick={() => setSelectedElement(element.id)}
                    >
                      {element.type === "qr" && (
                        <QRCodeSVG
                          value={element.content}
                          size={element.width * previewScale}
                          level="M"
                        />
                      )}
                      {element.type === "text" && (
                        <div
                          className="w-full h-full flex items-center text-black"
                          style={{
                            fontSize: `${element.fontSize * previewScale}px`,
                          }}
                        >
                          {element.content}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Column - Properties & Print Settings */}
          <div className="w-80 space-y-4">
            {/* Element Properties */}
            {selectedEl && (
              <Card>
                <CardHeader>
                  <CardTitle className="text-sm flex items-center justify-between">
                    Element Eigenschaften
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => deleteElement(selectedEl.id)}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {selectedEl.type === "text" && (
                    <div>
                      <Label>Inhalt</Label>
                      <Input
                        value={selectedEl.content}
                        onChange={(e) =>
                          updateElement(selectedEl.id, {
                            content: e.target.value,
                          })
                        }
                      />
                    </div>
                  )}

                  <div>
                    <Label>Schriftgre: {selectedEl.fontSize}px</Label>
                    <Slider
                      value={[selectedEl.fontSize]}
                      onValueChange={(value) =>
                        updateElement(selectedEl.id, { fontSize: value[0] })
                      }
                      max={24}
                      min={6}
                      step={1}
                      className="mt-2"
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-2">
                    <div>
                      <Label>X Position</Label>
                      <Input
                        type="number"
                        value={selectedEl.x}
                        onChange={(e) =>
                          updateElement(selectedEl.id, {
                            x: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                    <div>
                      <Label>Y Position</Label>
                      <Input
                        type="number"
                        value={selectedEl.y}
                        onChange={(e) =>
                          updateElement(selectedEl.id, {
                            y: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-2">
                    <div>
                      <Label>Breite</Label>
                      <Input
                        type="number"
                        value={selectedEl.width}
                        onChange={(e) =>
                          updateElement(selectedEl.id, {
                            width: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                    <div>
                      <Label>Hhe</Label>
                      <Input
                        type="number"
                        value={selectedEl.height}
                        onChange={(e) =>
                          updateElement(selectedEl.id, {
                            height: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}

            {/* Print Settings */}
            <Card>
              <CardHeader>
                <CardTitle className="text-sm flex items-center gap-2">
                  <Settings className="h-4 w-4" />
                  Druckeinstellungen
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label>Blattformat</Label>
                  <Select
                    value={printSettings.paperFormat}
                    onValueChange={(value: "A4" | "Letter" | "Custom") =>
                      setPrintSettings({ ...printSettings, paperFormat: value })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="A4">A4</SelectItem>
                      <SelectItem value="Letter">Letter</SelectItem>
                      <SelectItem value="Custom">Custom</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {printSettings.paperFormat === "Custom" && (
                  <div className="grid grid-cols-2 gap-2">
                    <div>
                      <Label>Blattbreite (mm)</Label>
                      <Input
                        type="number"
                        value={printSettings.customWidth}
                        onChange={(e) =>
                          setPrintSettings({
                            ...printSettings,
                            customWidth: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                    <div>
                      <Label>Blatthhe (mm)</Label>
                      <Input
                        type="number"
                        value={printSettings.customHeight}
                        onChange={(e) =>
                          setPrintSettings({
                            ...printSettings,
                            customHeight: Number(e.target.value),
                          })
                        }
                      />
                    </div>
                  </div>
                )}

                <div className="grid grid-cols-2 gap-2">
                  <div>
                    <Label>Etiketten pro Reihe</Label>
                    <Input
                      type="number"
                      value={printSettings.labelsPerRow}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          labelsPerRow: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                  <div>
                    <Label>Etiketten pro Spalte</Label>
                    <Input
                      type="number"
                      value={printSettings.labelsPerColumn}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          labelsPerColumn: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-2">
                  <div>
                    <Label>Etikettenbreite (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.labelWidth}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          labelWidth: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                  <div>
                    <Label>Etikettenhhe (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.labelHeight}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          labelHeight: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-2">
                  <div>
                    <Label>Oberer Rand (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.topMargin}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          topMargin: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                  <div>
                    <Label>Linker Rand (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.leftMargin}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          leftMargin: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-2">
                  <div>
                    <Label>Horizontaler Abstand (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.horizontalSpacing}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          horizontalSpacing: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                  <div>
                    <Label>Vertikaler Abstand (mm)</Label>
                    <Input
                      type="number"
                      value={printSettings.verticalSpacing}
                      onChange={(e) =>
                        setPrintSettings({
                          ...printSettings,
                          verticalSpacing: Number(e.target.value),
                        })
                      }
                    />
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  )
}
